/**
- Font size (paragraph): 15px
- Family: [Outfit](https://fonts.google.com/specimen/Outfit)
- Weights: 400, 700
*/
:root {
  --font-body: "Outfit", sans-serif;
  --font-body-size: 0.9375rem;
  --font-attribution-size: .75rem;
  --font-w-400: 400;
  --font-w-700: 700;
}

/**
- White: hsl(0, 0%, 100%)
- Light gray: hsl(212, 45%, 89%)
- Grayish blue: hsl(220, 15%, 55%)
- Dark blue: hsl(218, 44%, 22%)

*/
:root {
  --white: hsl(0, 0%, 100%);
  --light-gray: hsl(212, 45%, 89%);
  --grayish-blue: hsl(220, 15%, 55%);
  --dark-blue: hsl(218, 44%, 22%);
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  background-color: var(--light-gray);
  min-height: 100vh;
}

h1,
h2,
h3,
p {
  margin: 0;
}

h1 {
  font-size: 1.4rem;
}

a,
a:visited,
a:active {
  text-decoration: none;
}

body {
  font-family: var(--font-body);
  font-size: var(--font-body-size);
}

.flex {
  display: flex;
}

.f-jc-c {
  justify-content: center;
}

.f-ai-c {
  align-items: center;
}

.f-fd-col {
  flex-direction: column;
}

body {
  padding: 1rem;
}

.card {
  max-width: 20rem;
  background-color: var(--white);
  border-radius: 1.125rem;
  padding: 1rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  flex-wrap: wrap;
  box-shadow: 0 1.5rem 1.25rem rgba(0, 0, 0, 0.05);
}
.card__img {
  flex-grow: 1;
  flex-shrink: 1;
  width: 100%;
}
.card__content {
  display: flex;
  flex-direction: column;
  align-items: center;
  flex-wrap: wrap;
  padding: 0.8rem;
  margin-bottom: 1rem;
}
.card__title, .card__text {
  text-align: center;
  margin-top: 0.8rem;
}
.card__title {
  color: var(--dark-blue);
  font-weight: var(--font-w-700);
}
.card__text {
  color: var(--grayish-blue);
}

.attribution {
  font-size: var(--font-attribution-size);
  text-align: center;
  margin-top: 1rem;
}
.attribution a {
  color: #3e52a3;
}